import "./projects.scss";
import React, { useContext } from "react";
import { ContextApp } from "../../context/contextApp";
import CardProject from "../../components/cardProject/cardProject";
import projectsData from "./projectsData";

export default function Projects() {
  const { darkState } = useContext(ContextApp);
  const darkModeTxt = darkState ? "darkTxtProject" : "lightTxtProject";

  return (
    <section className="projectsContainer">
      <div className={`txtProjectsContainer ${darkModeTxt}`}>
        <h2>My projects</h2>
        <p>
          These projects are a show of my skills as a Frond end developer. In
          these apps, I use different libraries, frameworks, and resources to
          develop them. Also, I used agile methodologies to ease the process of
          creation and manage developing philosophies like mobile-first.
        </p>
      </div>
      <div className="cardContainer">
        {projectsData.map((i) => (
          <CardProject {...i} key={i.id} />
        ))}
      </div>
    </section>
  );
}
