import "./itsMe.scss";
import React, { useContext } from "react";
import { ContextApp } from "../../context/contextApp";
import useMouse from '@react-hook/mouse-position';

// import images
import meYellow01 from "../../assets/me_01.svg";
import meYellow02 from "../../assets/me_02.svg";
import meYellow03 from "../../assets/me_03.svg";
import meBlue01 from "../../assets/me_06.svg";
import meBlue02 from "../../assets/me_05.svg";
import meBlue03 from "../../assets/me_04.svg";

export default function ItsMe() {
  const { darkState } = useContext(ContextApp);
  const darkModeTxt = darkState ? "darkHomeTxt" : "lightHomeTxt";
  const ref = React.useRef(null);
  const mouse = useMouse(ref, {
    enterDelay: 100,
    leaveDelay: 100,
  })

  function imgAnimated(){
    const screenWidth = window.screen.width
    const mouseLocation = mouse.x

    if(mouseLocation < screenWidth / 4){      
      return darkState ? meYellow01 : meBlue01;
    }else if(mouseLocation < screenWidth / 2){      
      return darkState ? meYellow02 : meBlue02;
    }else{
      return darkState ? meYellow03 : meBlue03;
    }
  }

  return (
    <section className="itsMeContainer" ref={ref}>
      <header className="headerContainer">
        <div className="imgContainer">
          <img src={imgAnimated()} alt="Its me Jimmy" />
        </div>
        <div className={`txtContainer ${darkModeTxt}`}>
          <h2>
            Hello<span>,</span> world<span>!</span>
          </h2>
          <h1>I’m Jimmy.</h1>
          <p>
            A newborn in the front end, developer and graphic design from
            Bogotá, Colombia.
          </p>
        </div>
      </header>
    </section>
  );
}
